<template>
  <div class="hot-outer" v-if="detail">
    <div class="hotop">
      <div class="hotop-title">
        <div class="title-icon"></div>
        <div class="update">更新日期：{{ datetime | dateFormat }}</div>
      </div>
    </div>
    <ul>
      <SongList
        v-for="(item, index) in detail.tracks.slice(0, 30)"
        :key="item.id"
        :item="item"
        @play-song="
          $emit('play-song', item);
          $emit('mask-box', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt: index < 3 }"
        >{{ index + 1 }}
      </SongList>
    </ul>
    <div class="openapp">
      <div class="download">打开APP，发现更多好音乐 ></div>
    </div>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";

export default {
  name: "Hot",
  components: {
    SongList,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data() {
    return {
      detail: null,
      datetime: new Date(),
    };
  },
  filters: {
    dateFormat(value) {
      const time = new Date(value);
      const MM = time.getMonth() + 1;
      const DD = time.getDate();
      return `${MM}月${DD}日`;
    },
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail/toplist?id=3778678")
      .then((res) => {
        // console.log(res);
        this.detail = res.data.playlist;
      });
  },
};
</script>

